import React from 'react';
import PropTypes from 'prop-types';
import { useLocation } from 'react-router-dom';

import { __t } from '@mssgme/shared';
import { Spinner } from '@mssgme/ui';
import { useEventCallback } from '@mssgme/hooks';

import { ThemedModal } from '../../UI';
import { useCoreContext, useFrontAPI, useSavedOrders } from '../../hooks';
import { useOrder } from '../useOrder';
import { OrderViewPage } from './OrderViewPage';
import { ShoppingCartPage } from './ShoppingCartPage';

import styles from './UserOrderPage.scss';

export const UserOrderPage = ({ orderId }) => {
    const api = useFrontAPI();
    const { state } = useLocation();
    const { router } = useCoreContext();
    const { reload, order, loading, error, isNotFound } = useOrder(orderId);
    const { removeOrderId } = useSavedOrders();
    const backPath = state?.backPath || router.toHome();

    const handleClosing = useEventCallback(async () => {
        if (!order.items.length) {
            removeOrderId(order._id);

            return api.orders.close({ orderId });
        }
    });

    if (!order) {
        return null;
    }

    const title = order.finalized ? `${__t('order_term')} #${order.feedback.order}` : __t('checkout');
    const status = loading ? <Spinner /> : (error && (
        <div className={styles.error}>
            {isNotFound ? __t('order_not_found') : error.message}
        </div>
    ));

    return (
        <ThemedModal overlay compact padding title={title} onClosing={handleClosing} onClose={backPath}>
            {(handleClose) => (
                status || (
                    order.finalized ? (
                        <OrderViewPage order={order} isFetching={loading} reload={reload} />
                    ) : (
                        <ShoppingCartPage order={order} onClose={handleClose} />
                    )
                )
            )}
        </ThemedModal>
    );
};

UserOrderPage.propTypes = {
    orderId: PropTypes.string.isRequired,
};
